.header {
  font-size: 4vw;
  text-align: center;
  font-weight: bold;
}
.explaination {
  color: #888;
  text-align: center;
  margin-bottom: 25px;
}

.featureList {
  max-width: 50%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  list-style: none;
  margin: 0;
  margin: auto;
  padding: 0;
}
.featureListItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px;
  padding: 10px;
  font-size: 1.25em;
  transition: transform 250ms ease !important;
}
.featureListItem:hover {
  transform: translateY(-5px);
}
