.navbar {
  background-color: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(5px);
  box-shadow: 0px 0px 1px rgba(255, 255, 255, 0.5),
    2px 2px 5px rgba(0, 0, 0, 0.5);
}

.navLinks {
  margin: 0;
  padding: 0;
  list-style: none;
}
.navLinks li a {
  margin: 0 10px;
  text-decoration: none;
  color: #333;
}
.userProfile {
  width: 110px;
  height: 80px;
  border-radius: 50%;
  cursor: pointer;
}
.userProfile img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.dropdown {
  position: absolute;
  z-index: 2;
  width: 120px;
  background-color: #ddd;
}

.dropdown ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.dropdown li {
  margin-bottom: 15px;
}
.dropdown ul a {
  text-decoration: none;
  color: #333;
}
